import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useLocation, useNavigate } from 'react-router-dom';
import { 
  Calendar, 
  Clock, 
  MapPin, 
  User, 
  AlertTriangle, 
  Info, 
  CheckCircle, 
  Bell,
  Download,
  Share2,
  Bookmark,
  BookmarkCheck
} from 'lucide-react';
import { useCPMI } from '../../../context/CPMIContext';
import BackHeader from '../../../layout/BackHeader';

const NotifikasiDetail = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { cpmi } = useCPMI();
  const notifikasi = location.state?.notifikasi;
  const [isBookmarked, setIsBookmarked] = useState(false);

  // If no notifikasi data, redirect back
  if (!notifikasi) {
    navigate('/dashboard/cpmi/notifikasi');
    return null;
  }

  const getPriorityColor = (prioritas) => {
    switch (prioritas) {
      case 'urgent':
        return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200';
      case 'normal':
        return 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200';
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200';
    }
  };

  const getPriorityIcon = (prioritas) => {
    switch (prioritas) {
      case 'urgent':
        return { Icon: AlertTriangle, className: "text-red-600" };
      case 'normal':
        return { Icon: Info, className: "text-blue-600" };
      default:
        return { Icon: Bell, className: "text-gray-600" };
    }
  };

  const getTypeColor = (tipe) => {
    switch (tipe) {
      case 'jadwal':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200';
      case 'informasi':
        return 'bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200';
      case 'pengumuman':
        return 'bg-orange-100 text-orange-800 dark:bg-orange-900 dark:text-orange-200';
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200';
    }
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('id-ID', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  const formatTime = (timeString) => {
    return timeString;
  };

  const handleBookmark = () => {
    setIsBookmarked(!isBookmarked);
    // Here you would typically save to context or localStorage
  };

  const handleShare = () => {
    if (navigator.share) {
      navigator.share({
        title: notifikasi.judul,
        text: notifikasi.isi,
        url: window.location.href
      });
    } else {
      // Fallback for browsers that don't support Web Share API
      navigator.clipboard.writeText(`${notifikasi.judul}\n\n${notifikasi.isi}`);
      alert('Konten telah disalin ke clipboard!');
    }
  };

  const handleSaveToCalendar = () => {
    if (notifikasi.tipe === 'jadwal' && notifikasi.tanggal && notifikasi.jam) {
      const startDate = new Date(`${notifikasi.tanggal}T${notifikasi.jam}:00`);
      const endDate = new Date(startDate.getTime() + 2 * 60 * 60 * 1000); // 2 hours later
      
      const googleCalendarUrl = `https://calendar.google.com/calendar/render?action=TEMPLATE&text=${encodeURIComponent(notifikasi.judul)}&dates=${startDate.toISOString().replace(/[-:]/g, '').split('.')[0]}Z/${endDate.toISOString().replace(/[-:]/g, '').split('.')[0]}Z&details=${encodeURIComponent(notifikasi.isi)}&location=${encodeURIComponent(notifikasi.lokasi || 'LPK Bahana Mega Prestasi')}`;
      
      window.open(googleCalendarUrl, '_blank');
    }
  };

  const getDetailedContent = () => {
    // Enhanced content based on notification type
    const baseContent = notifikasi.isi;
    
    if (notifikasi.tipe === 'jadwal') {
      return {
        content: baseContent,
        additionalInfo: [
          'Pastikan datang 15 menit sebelum waktu yang ditentukan',
          'Bawa dokumen yang diperlukan',
          'Berpakaian rapi dan sopan',
          'Jika ada kendala, segera hubungi pengajar'
        ]
      };
    } else if (notifikasi.tipe === 'pengumuman') {
      return {
        content: baseContent,
        additionalInfo: [
          'Informasi ini berlaku untuk semua peserta',
          'Simpan pengumuman ini untuk referensi',
          'Hubungi admin jika ada pertanyaan'
        ]
      };
    } else {
      return {
        content: baseContent,
        additionalInfo: [
          'Baca informasi dengan seksama',
          'Tanyakan jika ada yang kurang jelas'
        ]
      };
    }
  };

  const detailedContent = getDetailedContent();

  return (
    <div className="min-h-screen bg-light-body dark:bg-dark-body pb-20">
      <BackHeader title="Detail Notifikasi" />
      
      <div className="p-4 space-y-6">
        {/* Header Card */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="card p-6"
        >
          <div className="flex items-start justify-between mb-4">
            <div className="flex-1">
              <div className="flex items-center space-x-2 mb-3">
                <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getPriorityColor(notifikasi.prioritas)}`}>
                  {(() => {
                    const { Icon, className } = getPriorityIcon(notifikasi.prioritas);
                    return <Icon className={className} size={16} />;
                  })()}
                  <span className="ml-1 capitalize">{notifikasi.prioritas}</span>
                </span>
                <span className={`inline-flex px-2 py-1 rounded-full text-xs font-medium ${getTypeColor(notifikasi.tipe)}`}>
                  {notifikasi.tipe.charAt(0).toUpperCase() + notifikasi.tipe.slice(1)}
                </span>
              </div>
              <h1 className="text-xl font-bold text-light-text-main dark:text-dark-text-main mb-2">
                {notifikasi.judul}
              </h1>
              <p className="text-sm text-secondary">
                {formatDate(notifikasi.tanggal_dibuat)} • {notifikasi.pengirim}
              </p>
            </div>
            <div className="flex space-x-2">
              <button
                onClick={handleBookmark}
                className="p-2 rounded-lg bg-light-body dark:bg-dark-body hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
              >
                {isBookmarked ? (
                  <BookmarkCheck size={20} className="text-primary-600 dark:text-primary-400" />
                ) : (
                  <Bookmark size={20} className="text-gray-600 dark:text-gray-400" />
                )}
              </button>
              <button
                onClick={handleShare}
                className="p-2 rounded-lg bg-light-body dark:bg-dark-body hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
              >
                <Share2 size={20} className="text-gray-600 dark:text-gray-400" />
              </button>
            </div>
          </div>
        </motion.div>

        {/* Schedule Info (if applicable) */}
        {notifikasi.tipe === 'jadwal' && (notifikasi.tanggal || notifikasi.jam || notifikasi.lokasi) && (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.1, duration: 0.5 }}
            className="card p-6"
          >
            <h2 className="text-lg font-semibold text-light-text-main dark:text-dark-text-main mb-4 flex items-center">
              <Calendar size={20} className="mr-2" />
              Detail Jadwal
            </h2>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {notifikasi.tanggal && (
                <div className="flex items-center space-x-3">
                  <div className="w-10 h-10 bg-blue-100 dark:bg-blue-900 rounded-lg flex items-center justify-center">
                    <Calendar size={20} className="text-blue-600 dark:text-blue-400" />
                  </div>
                  <div>
                    <p className="text-sm text-secondary">Tanggal</p>
                    <p className="font-medium text-light-text-main dark:text-dark-text-main">
                      {formatDate(notifikasi.tanggal)}
                    </p>
                  </div>
                </div>
              )}

              {notifikasi.jam && (
                <div className="flex items-center space-x-3">
                  <div className="w-10 h-10 bg-green-100 dark:bg-green-900 rounded-lg flex items-center justify-center">
                    <Clock size={20} className="text-green-600 dark:text-green-400" />
                  </div>
                  <div>
                    <p className="text-sm text-secondary">Waktu</p>
                    <p className="font-medium text-light-text-main dark:text-dark-text-main">
                      {formatTime(notifikasi.jam)}
                    </p>
                  </div>
                </div>
              )}

              {notifikasi.lokasi && (
                <div className="flex items-center space-x-3">
                  <div className="w-10 h-10 bg-purple-100 dark:bg-purple-900 rounded-lg flex items-center justify-center">
                    <MapPin size={20} className="text-purple-600 dark:text-purple-400" />
                  </div>
                  <div>
                    <p className="text-sm text-secondary">Lokasi</p>
                    <p className="font-medium text-light-text-main dark:text-dark-text-main">
                      {notifikasi.lokasi}
                    </p>
                  </div>
                </div>
              )}

              {notifikasi.pengirim && (
                <div className="flex items-center space-x-3">
                  <div className="w-10 h-10 bg-orange-100 dark:bg-orange-900 rounded-lg flex items-center justify-center">
                    <User size={20} className="text-orange-600 dark:text-orange-400" />
                  </div>
                  <div>
                    <p className="text-sm text-secondary">Pengirim</p>
                    <p className="font-medium text-light-text-main dark:text-dark-text-main">
                      {notifikasi.pengirim}
                    </p>
                  </div>
                </div>
              )}
            </div>

            {notifikasi.tipe === 'jadwal' && (
              <div className="mt-4">
                <button
                  onClick={handleSaveToCalendar}
                  className="w-full bg-primary-600 hover:bg-primary-700 text-white py-3 px-4 rounded-lg font-medium transition-colors flex items-center justify-center space-x-2"
                >
                  <Download size={20} />
                  <span>Simpan ke Google Calendar</span>
                </button>
              </div>
            )}
          </motion.div>
        )}

        {/* Content */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2, duration: 0.5 }}
          className="card p-6"
        >
          <h2 className="text-lg font-semibold text-light-text-main dark:text-dark-text-main mb-4 flex items-center">
            <Info size={20} className="mr-2" />
            Isi {notifikasi.tipe === 'jadwal' ? 'Jadwal' : notifikasi.tipe === 'pengumuman' ? 'Pengumuman' : 'Informasi'}
          </h2>
          <div className="prose prose-sm max-w-none text-secondary">
            <p className="leading-relaxed whitespace-pre-line">
              {detailedContent.content}
            </p>
          </div>
        </motion.div>

        {/* Additional Info */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3, duration: 0.5 }}
          className="card p-6"
        >
          <h2 className="text-lg font-semibold text-light-text-main dark:text-dark-text-main mb-4 flex items-center">
            <CheckCircle size={20} className="mr-2" />
            Hal yang Perlu Diperhatikan
          </h2>
          <div className="space-y-3">
            {detailedContent.additionalInfo.map((info, index) => (
              <div key={index} className="flex items-start space-x-3">
                <div className="w-6 h-6 bg-primary-100 dark:bg-primary-900 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                  <CheckCircle size={14} className="text-primary-600 dark:text-primary-400" />
                </div>
                <p className="text-secondary">{info}</p>
              </div>
            ))}
          </div>
        </motion.div>

        {/* Priority Alert */}
        {notifikasi.prioritas === 'urgent' && (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.4, duration: 0.5 }}
            className="card p-4 border-l-4 border-red-500 bg-red-50 dark:bg-red-900/20"
          >
            <div className="flex items-center space-x-3">
              <AlertTriangle size={20} className="text-red-600 dark:text-red-400" />
              <div>
                <p className="font-medium text-light-text-main dark:text-dark-text-main">
                  Notifikasi Penting
                </p>
                <p className="text-sm text-secondary">
                  Harap segera ditindaklanjuti dan jangan sampai terlewat.
                </p>
              </div>
            </div>
          </motion.div>
        )}
      </div>
    </div>
  );
};

export default NotifikasiDetail;