      aktivitas: 'Hari ini saya fokus pada pembersihan mendalam rumah. Saya membersihkan jendela-jendela, menyapu dan mengepel semua ruangan. Saya juga membersihkan kamar mandi dengan teliti. Siang hari saya membantu majikan menyiapkan makan siang dan membersihkan dapur setelahnya. Sore hari saya belajar cara menggunakan mesin cuci otomatis.',
