import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import { 
  Bell, 
  BellOff, 
  MessageSquare, 
  Calendar, 
  Users, 
  AlertTriangle,
  Check,
  X,
  Settings,
  Smartphone,
  Monitor,
  Volume2,
  VolumeX,
  UserCheck,
  FileText,
  Shield,
  MapPin,
  Mic,
  Camera
} from 'lucide-react';
import { useTheme } from '../../../context/ThemeContext';
import BackHeader from '../../../layout/BackHeader';

const NotificationSettings = () => {
  const { isDarkMode } = useTheme();
  const navigate = useNavigate();


  // State untuk izin browser
  const [browserPermissions, setBrowserPermissions] = useState({
    location: 'default',
    notification: 'default',
    media: 'default',
    camera: 'default'
  });

  const [isLoading, setIsLoading] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);

  // Fungsi untuk request permission browser
  const requestPermission = async (type) => {
    let permission = 'denied';
    
    try {
      switch (type) {
        case 'notification':
          if ('Notification' in window) {
            permission = await Notification.requestPermission();
          }
          break;
        case 'location':
          if ('geolocation' in navigator) {
            await new Promise((resolve, reject) => {
              navigator.geolocation.getCurrentPosition(resolve, reject);
            });
            permission = 'granted';
          }
          break;
        case 'media':
          if ('mediaDevices' in navigator) {
            await navigator.mediaDevices.getUserMedia({ audio: true });
            permission = 'granted';
          }
          break;
        case 'camera':
          if ('mediaDevices' in navigator) {
            await navigator.mediaDevices.getUserMedia({ video: true });
            permission = 'granted';
          }
          break;
      }
    } catch (error) {
      permission = 'denied';
    }
    
    setBrowserPermissions(prev => ({
      ...prev,
      [type]: permission
    }));
  };

  // Fungsi untuk check current permissions
  const checkPermissions = async () => {
    const permissions = { ...browserPermissions };
    
    // Check notification permission
    if ('Notification' in window) {
      permissions.notification = Notification.permission;
    }
    
    // Check location permission
    if ('permissions' in navigator) {
      try {
        const locationPermission = await navigator.permissions.query({ name: 'geolocation' });
        permissions.location = locationPermission.state;
      } catch (error) {
        permissions.location = 'default';
      }
    }
    
    setBrowserPermissions(permissions);
  };

  // Check permissions on component mount
  useEffect(() => {
    checkPermissions();
  }, []);

  // Save settings
  const handleSaveSettings = () => {
    setIsLoading(true);
    
    // Simulate API call
    setTimeout(() => {
      setIsLoading(false);
      setShowSuccess(true);
      
      // Hide success message after 3 seconds
      setTimeout(() => {
        setShowSuccess(false);
      }, 3000);
    }, 1000);
  };



  return (
    <div className="min-h-screen bg-light-body dark:bg-dark-body">
      <BackHeader title="Pengaturan Notifikasi" />
      
      <div className="p-4 pt-5 pb-20">


        {/* Browser Permissions */}
        <div className="bg-light-card dark:bg-dark-card rounded-xl p-6 border border-light-border dark:border-dark-border">
          <h3 className="font-semibold text-light-text-main dark:text-dark-text-main mb-6">
            Izin Browser
          </h3>
          <div className="space-y-4">
            {/* Location Permission */}
            <div className="flex items-center justify-between p-4 bg-light-body dark:bg-dark-body rounded-lg">
              <div className="flex items-center space-x-3">
                <div className="p-2 bg-blue-100 dark:bg-blue-900/30 rounded-lg">
                  <MapPin className="w-5 h-5 text-blue-600 dark:text-blue-400" />
                </div>
                <div>
                  <span className="text-light-text-main dark:text-dark-text-main font-medium">Izin Lokasi</span>
                  <p className="text-sm text-light-text-secondary dark:text-dark-text-secondary">
                    Status: {browserPermissions.location === 'granted' ? 'Diizinkan' : 
                            browserPermissions.location === 'denied' ? 'Ditolak' : 'Belum Diatur'}
                  </p>
                </div>
              </div>
              {browserPermissions.location !== 'granted' && (
                <button
                  onClick={() => requestPermission('location')}
                  className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors text-sm"
                >
                  Izinkan
                </button>
              )}
            </div>

            {/* Notification Permission */}
            <div className="flex items-center justify-between p-4 bg-light-body dark:bg-dark-body rounded-lg">
              <div className="flex items-center space-x-3">
                <div className="p-2 bg-green-100 dark:bg-green-900/30 rounded-lg">
                  <Bell className="w-5 h-5 text-green-600 dark:text-green-400" />
                </div>
                <div>
                  <span className="text-light-text-main dark:text-dark-text-main font-medium">Izin Notifikasi</span>
                  <p className="text-sm text-light-text-secondary dark:text-dark-text-secondary">
                    Status: {browserPermissions.notification === 'granted' ? 'Diizinkan' : 
                            browserPermissions.notification === 'denied' ? 'Ditolak' : 'Belum Diatur'}
                  </p>
                </div>
              </div>
              {browserPermissions.notification !== 'granted' && (
                <button
                  onClick={() => requestPermission('notification')}
                  className="px-4 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors text-sm"
                >
                  Izinkan
                </button>
              )}
            </div>

            {/* Media Permission */}
            <div className="flex items-center justify-between p-4 bg-light-body dark:bg-dark-body rounded-lg">
              <div className="flex items-center space-x-3">
                <div className="p-2 bg-purple-100 dark:bg-purple-900/30 rounded-lg">
                  <Mic className="w-5 h-5 text-purple-600 dark:text-purple-400" />
                </div>
                <div>
                  <span className="text-light-text-main dark:text-dark-text-main font-medium">Izin Media</span>
                  <p className="text-sm text-light-text-secondary dark:text-dark-text-secondary">
                    Status: {browserPermissions.media === 'granted' ? 'Diizinkan' : 
                            browserPermissions.media === 'denied' ? 'Ditolak' : 'Belum Diatur'}
                  </p>
                </div>
              </div>
              {browserPermissions.media !== 'granted' && (
                <button
                  onClick={() => requestPermission('media')}
                  className="px-4 py-2 bg-purple-500 text-white rounded-lg hover:bg-purple-600 transition-colors text-sm"
                >
                  Izinkan
                </button>
              )}
            </div>

            {/* Camera Permission */}
            <div className="flex items-center justify-between p-4 bg-light-body dark:bg-dark-body rounded-lg">
              <div className="flex items-center space-x-3">
                <div className="p-2 bg-orange-100 dark:bg-orange-900/30 rounded-lg">
                  <Camera className="w-5 h-5 text-orange-600 dark:text-orange-400" />
                </div>
                <div>
                  <span className="text-light-text-main dark:text-dark-text-main font-medium">Izin Kamera</span>
                  <p className="text-sm text-light-text-secondary dark:text-dark-text-secondary">
                    Status: {browserPermissions.camera === 'granted' ? 'Diizinkan' : 
                            browserPermissions.camera === 'denied' ? 'Ditolak' : 'Belum Diatur'}
                  </p>
                </div>
              </div>
              {browserPermissions.camera !== 'granted' && (
                <button
                  onClick={() => requestPermission('camera')}
                  className="px-4 py-2 bg-orange-500 text-white rounded-lg hover:bg-orange-600 transition-colors text-sm"
                >
                  Izinkan
                </button>
              )}
            </div>
          </div>
        </div>

        {/* Save Button */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.9 }}
          className="mt-8"
        >
          <button
            onClick={handleSaveSettings}
            className="w-full flex items-center justify-center space-x-2 px-6 py-4 bg-blue-500 text-white rounded-xl hover:bg-blue-600 transition-colors font-medium"
          >
            <Check className="w-5 h-5" />
            <span>Simpan Pengaturan</span>
          </button>
        </motion.div>
      </div>
    </div>
  );
};

export default NotificationSettings;