import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import { 
  Search, 
  MessageCircle,
  Clock,
  Users,
  GraduationCap,
  User,
  Shield
} from 'lucide-react';
import { useTheme } from '../../../context/ThemeContext';
import BackHeader from '../../../layout/BackHeader';
import NavBottom from '../../../layout/NavBottom';

const Chat = () => {
  const { isDarkMode } = useTheme();
  const navigate = useNavigate();
  const [searchTerm, setSearchTerm] = useState('');

  // Dummy data chat untuk Admin
  const chatList = [
    {
      id: 1,
      nama: 'Bu Sari Wulandari',
      role: 'Pengajar',
      status: 'Online',
      avatar: null,
      lastMessage: 'Laporan kehadiran batch 15 sudah lengkap pak',
      lastTime: '14:30',
      unread: 2,
      online: true,
      kelas: 'Taiwan Batch 15'
    },
    {
      id: 2,
      nama: 'Pak Budi Santoso',
      role: 'Pengajar',
      status: 'Online',
      avatar: null,
      lastMessage: 'Evaluasi bulanan siap dilaksanakan besok',
      lastTime: '13:45',
      unread: 0,
      online: true,
      kelas: 'Taiwan Batch 14'
    },
    {
      id: 3,
      nama: 'Ahmad Rizki Pratama',
      role: 'CPMI',
      status: 'Piket',
      avatar: null,
      lastMessage: 'Pak, laporan piket hari ini sudah saya kirim',
      lastTime: '11:20',
      unread: 1,
      online: false,
      kelas: 'Taiwan Batch 15'
    },
    {
      id: 4,
      nama: 'Siti Nurhaliza',
      role: 'CPMI',
      status: 'Aktif',
      avatar: null,
      lastMessage: 'Terima kasih pak atas informasinya',
      lastTime: '10:15',
      unread: 0,
      online: true,
      kelas: 'Taiwan Batch 15'
    },
    {
      id: 5,
      nama: 'Ibu Chen Li Mei',
      role: 'Pengajar',
      status: 'Online',
      avatar: null,
      lastMessage: '学生们的中文进步很快！',
      lastTime: 'Kemarin',
      unread: 0,
      online: false,
      kelas: 'Taiwan Batch 16'
    },
    {
      id: 6,
      nama: 'Taiwan Batch 15',
      role: 'Grup',
      status: 'Aktif',
      avatar: null,
      lastMessage: 'Reminder: Interview besok jam 10:00',
      lastTime: 'Kemarin',
      unread: 3,
      online: true,
      members: 15
    }
  ];

  const filteredChats = chatList.filter(chat => 
    chat.nama.toLowerCase().includes(searchTerm.toLowerCase()) ||
    chat.role.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleChatClick = (chat) => {
    navigate(`/dashboard/admin/chat/${chat.id}`, { state: { chat } });
  };

  const getRoleIcon = (role) => {
    switch (role) {
      case 'Pengajar':
        return <GraduationCap className="w-4 h-4" />;
      case 'CPMI':
        return <User className="w-4 h-4" />;
      case 'Grup':
        return <Users className="w-4 h-4" />;
      default:
        return <Shield className="w-4 h-4" />;
    }
  };

  const getRoleColor = (role) => {
    switch (role) {
      case 'Pengajar':
        return 'bg-blue-100 dark:bg-blue-900/30 text-blue-700 dark:text-blue-300';
      case 'CPMI':
        return 'bg-green-100 dark:bg-green-900/30 text-green-700 dark:text-green-300';
      case 'Grup':
        return 'bg-purple-100 dark:bg-purple-900/30 text-purple-700 dark:text-purple-300';
      default:
        return 'bg-gray-100 dark:bg-gray-900/30 text-gray-700 dark:text-gray-300';
    }
  };

  return (
    <div className="min-h-screen bg-light-body dark:bg-dark-body pb-20">
      <BackHeader title="Chat Admin" />
      
      <div className="pt-5 px-4">
        {/* Search Bar */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-6"
        >
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-light-text-secondary dark:text-dark-text-secondary" />
            <input
              type="text"
              placeholder="Cari pengajar, CPMI, atau grup..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-3 border border-light-border dark:border-dark-border rounded-xl bg-light-card dark:bg-dark-card text-light-text-main dark:text-dark-text-main focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
          </div>
        </motion.div>

        {/* Chat List */}
        <div className="space-y-3">
          {filteredChats.length > 0 ? (
            filteredChats.map((chat, index) => (
              <motion.div
                key={chat.id}
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: index * 0.1 }}
                onClick={() => handleChatClick(chat)}
                className="bg-light-card dark:bg-dark-card rounded-xl p-4 border border-light-border dark:border-dark-border hover:shadow-md transition-all duration-200 cursor-pointer"
              >
                <div className="flex items-center space-x-4">
                  {/* Avatar with Online Status */}
                  <div className="relative">
                    <div className="w-12 h-12 bg-gradient-to-br from-blue-400 to-blue-600 rounded-full flex items-center justify-center text-white font-semibold text-lg">
                      {chat.nama.charAt(0)}
                    </div>
                    {/* Online Status Dot */}
                    <div className={`absolute -bottom-1 -right-1 w-4 h-4 border-2 border-white dark:border-dark-card rounded-full ${
                      chat.online ? 'bg-green-500' : 'bg-gray-400'
                    }`}></div>
                  </div>

                  {/* Chat Info */}
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between mb-1">
                      <div className="flex items-center space-x-2">
                        <h3 className="font-semibold text-light-text-main dark:text-dark-text-main truncate">
                          {chat.nama}
                        </h3>
                        <span className={`inline-flex items-center space-x-1 px-2 py-1 rounded-full text-xs font-medium ${getRoleColor(chat.role)}`}>
                          {getRoleIcon(chat.role)}
                          <span>{chat.role}</span>
                        </span>
                      </div>
                      <div className="flex items-center space-x-2">
                        <div className="flex items-center space-x-1">
                          <Clock className="w-3 h-3 text-light-text-secondary dark:text-dark-text-secondary" />
                          <span className="text-xs text-light-text-secondary dark:text-dark-text-secondary">
                            {chat.lastTime}
                          </span>
                        </div>
                        {chat.unread > 0 && (
                          <div className="bg-red-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">
                            {chat.unread}
                          </div>
                        )}
                      </div>
                    </div>
                    
                    <div className="flex items-center justify-between">
                      <p className="text-sm text-light-text-secondary dark:text-dark-text-secondary truncate flex-1 mr-2">
                        {chat.lastMessage}
                      </p>
                    </div>
                    
                    {/* Additional Info */}
                    <div className="flex items-center justify-between mt-2">
                      {chat.kelas && (
                        <span className="text-xs text-light-text-secondary dark:text-dark-text-secondary bg-light-body dark:bg-dark-body px-2 py-1 rounded-full">
                          {chat.kelas}
                        </span>
                      )}
                      {chat.members && (
                        <span className="text-xs text-light-text-secondary dark:text-dark-text-secondary">
                          {chat.members} anggota
                        </span>
                      )}
                      {chat.status && chat.role === 'CPMI' && (
                        <span className={`text-xs px-2 py-1 rounded-full ${
                          chat.status === 'Aktif' ? 'bg-green-100 dark:bg-green-900/30 text-green-700 dark:text-green-300' :
                          chat.status === 'Piket' ? 'bg-yellow-100 dark:bg-yellow-900/30 text-yellow-700 dark:text-yellow-300' :
                          'bg-gray-100 dark:bg-gray-900/30 text-gray-700 dark:text-gray-300'
                        }`}>
                          {chat.status}
                        </span>
                      )}
                    </div>
                  </div>
                </div>
              </motion.div>
            ))

          ) : (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              className="text-center py-12"
            >
              <MessageCircle className="w-16 h-16 text-light-text-secondary dark:text-dark-text-secondary mx-auto mb-4" />
              <h3 className="text-lg font-medium text-light-text-main dark:text-dark-text-main mb-2">
                Tidak ada chat ditemukan
              </h3>
              <p className="text-light-text-secondary dark:text-dark-text-secondary">
                Coba ubah kata kunci pencarian
              </p>
            </motion.div>
          )}
        </div>
      </div>

      <NavBottom />
    </div>
  );
};

export default Chat;