    kelas.nama.toLowerCase().includes(searchQuery.toLowerCase()) ||
    kelas.pengajar.toLowerCase().includes(searchQuery.toLowerCase()) ||
    kelas.alamat.toLowerCase().includes(searchQuery.toLowerCase())
