import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { 
  Moon, 
  Sun, 
  Bell, 
  Globe, 
  Shield, 
  HelpCircle, 
  Info, 
  LogOut,
  ChevronRight,
  User,
  Smartphone,
  Volume2,
  Eye,
  Download
} from 'lucide-react';
import { useTheme } from '../../../context/ThemeContext';
import { useCPMI } from '../../../context/CPMIContext';
import BackHeader from '../../../layout/BackHeader';
import CacheQuickAction from '../../../components/CacheQuickAction';

const Settings = () => {
  const { isDark, toggleTheme } = useTheme();
  const { cpmi } = useCPMI();
  const [notifications, setNotifications] = useState(true);
  const [soundEnabled, setSoundEnabled] = useState(true);
  const [autoDownload, setAutoDownload] = useState(false);
  const [language, setLanguage] = useState('id');

  const settingsGroups = [
    {
      title: 'Tampilan',
      items: [
        {
          id: 'theme',
          label: 'Mode Gelap',
          description: 'Ubah tampilan aplikasi',
          icon: isDark ? Moon : Sun,
          type: 'toggle',
          value: isDark,
          onChange: toggleTheme
        },
        {
          id: 'language',
          label: 'Bahasa',
          description: 'Indonesia',
          icon: Globe,
          type: 'select',
          value: language,
          options: [
            { value: 'id', label: 'Bahasa Indonesia' },
            { value: 'en', label: 'English' },
            { value: 'zh', label: '中文 (Chinese)' }
          ],
          onChange: setLanguage
        }
      ]
    },
    {
      title: 'Notifikasi',
      items: [
        {
          id: 'notifications',
          label: 'Notifikasi Push',
          description: 'Terima pemberitahuan penting',
          icon: Bell,
          type: 'toggle',
          value: notifications,
          onChange: setNotifications
        },
        {
          id: 'sound',
          label: 'Suara Notifikasi',
          description: 'Putar suara saat ada notifikasi',
          icon: Volume2,
          type: 'toggle',
          value: soundEnabled,
          onChange: setSoundEnabled
        }
      ]
    },
    {
      title: 'Data & Penyimpanan',
      items: [
        {
          id: 'auto_download',
          label: 'Unduh Otomatis',
          description: 'Unduh dokumen secara otomatis',
          icon: Download,
          type: 'toggle',
          value: autoDownload,
          onChange: setAutoDownload
        }
      ]
    },
    {
      title: 'Akun & Keamanan',
      items: [
        {
          id: 'profile',
          label: 'Informasi Profil',
          description: 'Kelola data pribadi Anda',
          icon: User,
          type: 'navigation',
          path: '/dashboard/cpmi/profil'
        },
        {
          id: 'privacy',
          label: 'Privasi & Keamanan',
          description: 'Pengaturan keamanan akun',
          icon: Shield,
          type: 'navigation'
        }
      ]
    },
    {
      title: 'Bantuan & Info',
      items: [
        {
          id: 'help',
          label: 'Pusat Bantuan',
          description: 'FAQ dan panduan penggunaan',
          icon: HelpCircle,
          type: 'navigation'
        },
        {
          id: 'about',
          label: 'Tentang Aplikasi',
          description: 'Versi 1.0.0',
          icon: Info,
          type: 'navigation'
        }
      ]
    }
  ];

  const handleToggle = (item) => {
    if (item.onChange) {
      item.onChange(!item.value);
    }
  };

  const handleNavigation = (path) => {
    if (path) {
      // Navigate to path
      console.log('Navigate to:', path);
    }
  };

  const handleLogout = () => {
    // Clear authentication and demo status
    localStorage.removeItem('isAuthenticated');
    localStorage.removeItem('demoStatus');
    localStorage.removeItem('selectedCPMI');
    
    // Redirect to login page
    window.location.href = '/login';
  };

  const renderSettingItem = (item) => {
    switch (item.type) {
      case 'toggle':
        return (
          <motion.div
            key={item.id}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border rounded-xl p-4 mb-3"
          >
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-primary-100 dark:bg-primary-900/20 rounded-lg flex items-center justify-center">
                  <item.icon size={20} className="text-primary-600 dark:text-primary-400" />
                </div>
                <div className="flex-1">
                  <h3 className="font-medium text-light-text-main dark:text-dark-text-main">
                    {item.label}
                  </h3>
                  <p className="text-sm text-secondary">
                    {item.description}
                  </p>
                </div>
              </div>
              <button
                onClick={() => handleToggle(item)}
                className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                  item.value
                    ? 'bg-primary-500'
                    : 'bg-gray-300 dark:bg-gray-600'
                }`}
              >
                <span
                  className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
                    item.value ? 'translate-x-6' : 'translate-x-1'
                  }`}
                />
              </button>
            </div>
          </motion.div>
        );

      case 'select':
        return (
          <motion.div
            key={item.id}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border rounded-xl p-4 mb-3"
          >
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-primary-100 dark:bg-primary-900/20 rounded-lg flex items-center justify-center">
                  <item.icon size={20} className="text-primary-600 dark:text-primary-400" />
                </div>
                <div className="flex-1">
                  <h3 className="font-medium text-light-text-main dark:text-dark-text-main">
                    {item.label}
                  </h3>
                  <p className="text-sm text-secondary">
                    {item.description}
                  </p>
                </div>
              </div>
              <ChevronRight size={18} className="text-secondary" />
            </div>
          </motion.div>
        );

      case 'navigation':
        return (
          <motion.div
            key={item.id}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border rounded-xl p-4 mb-3"
          >
            <button
              onClick={() => handleNavigation(item.path)}
              className="w-full flex items-center justify-between hover:bg-light-border dark:hover:bg-dark-border rounded-lg transition-colors p-1 -m-1"
            >
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-primary-100 dark:bg-primary-900/20 rounded-lg flex items-center justify-center">
                  <item.icon size={20} className="text-primary-600 dark:text-primary-400" />
                </div>
                <div className="flex-1 text-left">
                  <h3 className="font-medium text-light-text-main dark:text-dark-text-main">
                    {item.label}
                  </h3>
                  <p className="text-sm text-secondary">
                    {item.description}
                  </p>
                </div>
              </div>
              <ChevronRight size={18} className="text-secondary" />
            </button>
          </motion.div>
        );

      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen bg-light-body dark:bg-dark-body">
      <BackHeader title="Setting" />
      
      <div className="p-4 pb-20">
        {/* User Info Card */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="bg-gradient-to-r from-primary-500 to-primary-600 rounded-xl p-4 mb-6 text-white"
        >
          <div className="flex items-center space-x-3">
            <div className="w-12 h-12 bg-white/20 rounded-full flex items-center justify-center">
              <User size={24} className="text-white" />
            </div>
            <div className="flex-1">
              <h3 className="font-semibold text-lg">{cpmi.nama}</h3>
              <p className="text-white/80 text-sm">{cpmi.kelas} • Status: {cpmi.status}</p>
            </div>
            <div className="w-8 h-8 bg-white/20 rounded-full flex items-center justify-center">
              <Smartphone size={16} className="text-white" />
            </div>
          </div>
        </motion.div>

        {/* Quick Actions */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.1 }}
          className="mb-6"
        >
          <h2 className="text-lg font-semibold text-light-text-main dark:text-dark-text-main mb-3 px-1">
            Aksi Cepat
          </h2>
          <CacheQuickAction />
        </motion.div>

        {/* Settings Groups */}
        {settingsGroups.map((group, groupIndex) => (
          <motion.div
            key={group.title}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: (groupIndex + 1) * 0.1 }}
            className="mb-6"
          >
            <h2 className="text-lg font-semibold text-light-text-main dark:text-dark-text-main mb-3 px-1">
              {group.title}
            </h2>
            {group.items.map(renderSettingItem)}
          </motion.div>
        ))}

        {/* Logout Button */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.5 }}
          className="mt-8"
        >
          <button
            onClick={handleLogout}
            className="w-full bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-xl p-4 flex items-center justify-center space-x-3 hover:bg-red-100 dark:hover:bg-red-900/30 transition-colors"
          >
            <LogOut size={20} className="text-red-600 dark:text-red-400" />
            <span className="font-medium text-red-600 dark:text-red-400">
              Keluar dari Akun
            </span>
          </button>
        </motion.div>

        {/* App Info */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.6 }}
          className="mt-6 text-center"
        >
          <p className="text-xs text-secondary">
            LPK Bahana Mega Prestasi • Versi 1.0.0
          </p>
          <p className="text-xs text-secondary mt-1">
            © 2024 - Sistem Absensi CPMI Taiwan
          </p>
        </motion.div>
      </div>
    </div>
  );
};

export default Settings;